import axios from 'axios';

// To mt instance ca Axios vi base URL
const api = axios.create({
    baseURL: 'http://localhost:3001/auth',
    headers: {
        'Content-Type': 'application/json',
    },
});

// Hm lu token vo localStorage
const saveToken = (token) => {
    localStorage.setItem('authToken', token);
};

// Hm ly token t localStorage
const getToken = () => {
    return localStorage.getItem('authToken');
};

// Thm interceptor  t ng gn token vo header
api.interceptors.request.use((config) => {
    const token = getToken();
    if (token) {
        config.headers.Authorization = `Bearer ${token}`;
    }
    return config;
}, (error) => {
    return Promise.reject(error);
});

/**
 * ng k ti khon.
 * @param {Object} userData - D liu ng k.
 * @param {string} userData.name - Tn y .
 * @param {string} userData.email - Email.
 * @param {string} userData.password - Mt khu.
 * @param {Date} userData.birthDate - Ngy sinh.
 * @param {string} userData.gender - Gii tnh.
 * @returns {Promise<Object>} D liu phn hi t API.
 */
export const register = async (userData) => {
    try {
        const response = await api.post('/register', {
            name: userData.name,
            email: userData.email,
            password: userData.password,
            birthDate: userData.birthDate,
            gender: userData.gender
        }
        );
        return response.data;
    } catch (error) {
        console.error('Register Error: ', error.response?.data || error.message);
        throw error.response?.data || error.message;
    }
};

/**
 * ng nhp ti khon.
 * @param {Object} credentials - Thng tin ng nhp.
 * @param {string} credentials.email - Email.
 * @param {string} credentials.password - Mt khu.
 * @returns {Promise<Object>} D liu phn hi t API (bao gm token).
 */
export const login = async (credentials) => {
    try {
        const response = await api.post('/login', credentials);
        const { token, user } = response.data;

        // Lu token vo localStorage
        saveToken(token);

        return { token, user };
    } catch (error) {
        console.error('Login Error: ', error.response?.data || error.message);
        throw error.response?.data || error.message;
    }
};

/**
 * ng xut ti khon (xa token).
 */
export const logout = () => {
    localStorage.removeItem('authToken');
};

export default api;
